

<template>
  <div class="wraper">
    <div class="lable">
      <p>asdadada1</p>
      <p>{{datas.lable}}</p>
    </div>
    <div class="left">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#madam" />
      </svg>
    </div>
    <div class="right">
      <div class="rightNum" :style="{width:percent+'%',background:datas.leftCol}">
        <p class="num">asdasda</p>
        <p class="num">{{datas.leftPer}}</p>
      </div>

      <div class="rightArrowWraper" :style="{left:percent+'%'}">
        <i class="top"></i>
      </div>
      <div
        class="leftNum"
        :style="{left:(percent)+'%',width:(100-percent)+'%',background:datas.rightCol}"
      >
        <p class="num">asdasda</p>
        <p class="num">{{datas.rightPer}}</p>
      </div>
    </div>
  </div>
</template>
<script>
/**
datas: {
       lable: "asdadasda131",
        leftPer: "90000",
        rightPer: "100000",
        rightCol: "blue",
        leftCol: "blueviolet"
      }
*/
export default {
  name: "PercentSlider",
  props: ["datas"],
  computed: {
    percent() {
      return (
        (this.datas.leftPer /
          (this.datas.leftPer * 1 + this.datas.rightPer * 1)) *
        100
      );
    }
  }
};
</script>
